html {
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
}

img {
  height: auto;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  //display: flex;
  //
  //width: 100%;
  //height: 100%;
  //
  //text-decoration: none;
  //
  //color: inherit;
  //
  //justify-content: center;
  //align-items: center;
}

.body-container {
  display: flex;
  flex-direction: column;

  align-items: center;

  margin: 0 auto;
}

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 738px;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

@media (max-width: $mobile-width) {
  .container {
    max-width: 280px;
  }
}
